.toDoList{
    margin:15px;
    padding: 10px;
    height: 180%;
    width: 25%;
    border: 2px solid black;
    border-radius: 10px;
    color:black ;
    background-color: burlywood;
}
.task{
    display: flex;
}
.title{
    display: flex;

}
.titleName{
    font-size: 80px;
    margin: 10px;
    font-family: cursive;

}
.input{
    display: flex;
}
.errorMessage{
    color: red;
}
.buttonFilter{
    display: flex;
    width: 150px;
}

.removeButton{
    background-color: #ff8d58;
}