.input {
    border: 2px solid black;
    border-radius: 10px;
    color: black;
    background-color: #ffdf9b;
    width: 200px;
    height: 20px;
    font-size: 20px;
    font-family: cursive;
}

.inputError{
    border: 2px solid black;
    border-radius: 10px;
    background-color: #ff6438;
    color: #61dafb;
}
.inputText{
    display: block;
}